import React, { useState } from 'react';
import {
  Button,
  Card,
  CardBody,
  CardTitle,
  FormGroup,
  Modal,
} from 'reactstrap';
import { Colxx } from '../../../../../components/common/CustomBootstrap';
import AddCustomQuestionModal from '../../../../../components/application/AddQuestionModal';
import CustomQuestionList from '../../../../../components/application/CustomQuestionList';
import IntlMessages from '../../../../../helpers/IntlMessages';

const AddCustomQuestion = ({ id, formValues, setFormValues,  }) => {
  const [openAddModal, setOpenAddModal] = useState(false);

  return (
    <Card className="mb-4">
      <CardBody>
        <CardTitle>
          <div className="lead h4">
            <IntlMessages id="challenges.add-question-title" />
          </div>
        </CardTitle>
        <FormGroup>
          <Button className="m-2" onClick={() => setOpenAddModal(true)}>
            <IntlMessages id="challenges.add-custom-question" />
          </Button>
        </FormGroup>
        <Modal
          isOpen={openAddModal}
          toggle={() => setOpenAddModal(!openAddModal)}
          wrapClassName="modal-right"
          backdrop="static"
        >
          <AddCustomQuestionModal
            id = {id}
            setOpenAddModal={setOpenAddModal}
            formValues={formValues}
            setFormValues={setFormValues}
          />
        </Modal>
        <Colxx xxs="12" md="12">
          <CustomQuestionList id={id} />
        </Colxx>
      </CardBody>
    </Card>
  );
};

export default AddCustomQuestion;
