import React from 'react';
import { /* Button, */ Card, CardBody,CardText } from 'reactstrap';

const QuestionCard = ({ data }) => {
  return (
    <Card className="d-flex flex-row mb-4">
      <div className=" d-flex flex-grow-1 min-width-zero">
        <CardBody className=" pl-4 align-self-center d-flex flex-column flex-lg-row justify-content-between min-width-zero">
          <div className="min-width-zero">
              {/* <CardSubtitle className="truncate mb-1">{data.name}</CardSubtitle> */}
            <CardText className="text-muted text-small mb-2">
              {data.customized_questions}
            </CardText>
            {/* <Button outline size="xs" color="danger">
              Remove
            </Button> */}
          </div>
        </CardBody>
      </div>
    </Card>
  );
};

export default QuestionCard;
