/* eslint-disable camelcase */
import React from 'react';
import { connect } from 'react-redux';

import AppError from '../../helpers/AppError';
import CustomQuestionCard from '../cards/CustomQuestionCard';

import { getHackathonByID } from '../../redux/actions';

const QuestionList = ({
  
  hackathonDetails,
  loading,
  error,
}) => {
 
  if (error) throw new AppError(error);

  return (
    <>
      

      {loading ? (
        <div className="d-flex justify-content-center m-4">
          <div className="loading" role="status">
            <span className="sr-only">Loading...</span>
          </div>
        </div>
      ) : (
        <>
          {hackathonDetails.custom_questions.map((item) => (
            <CustomQuestionCard key={item.id} data={item} />
          ))}
        </>
      )}
    </>
  );
};

const mapStateToProps = ({ authUser, hackathonData }) => {
  const { accessToken } = authUser;
  const { hackathonDetails, loading, error } = hackathonData;
  return { accessToken, hackathonDetails, loading, error };
};

export default connect(mapStateToProps, {
  getDetailsAction: getHackathonByID,
})(QuestionList);
