import React, { useState } from 'react';
import {
  Button,
  FormGroup,
  Input,
  Label,
  ModalBody,
  ModalHeader,
} from 'reactstrap';
import { connect } from 'react-redux';
import IntlMessages from '../../helpers/IntlMessages';
import instance from '../../helpers/axiosInstance';
import { Separator } from '../common/CustomBootstrap';
import StateButton from '../StateButton';

const AddCustomQuestionModal = ({ setOpenAddModal, accessToken, id}) => {
  const [question, setQuestion] = useState([]);

  const handleChange = (event) => {
    setQuestion(
      event.target.value
    );
  };

  const handleSubmit = async (newData) => {

    await instance({
      url: '/updateHackathon',
      method: 'POST',
      headers: {
        Authorization: accessToken,
      },
      data: {id,custom_question:[newData]},
      timeout: 100000,
    });

    setTimeout(() => {
      window.location.reload();
    }, 300);
  };

  return (
    <>
      <ModalHeader>
        <h2 className="lead">
          <IntlMessages id="challenges.modal.application-add-question-title" />
        </h2>
      </ModalHeader>

      <ModalBody>
        <FormGroup>
          <Label>
            <IntlMessages id="challenges.modal.question" />
          </Label>

          <Input
            type="text"
            placeholder='Enter your question here'
            onChange={(event) => handleChange(event, 'question')}
          />
        </FormGroup>

        <FormGroup className="row justify-content-end">
          <StateButton
            id="add-question-buttom"
            color="secondary"
            
            onClick={() => handleSubmit(question)}
            next={() => setOpenAddModal(false)}
            className="m-2"
          >
            <IntlMessages id="challenges.modal.application-add" />
          </StateButton>
          <Button
            color="danger"
            onClick={() => setOpenAddModal(false)}
            className="m-2"
          >
            <IntlMessages id="challenges.modal.application-cancel" />
          </Button>
        </FormGroup>

        <Separator className="mb-4" />

      </ModalBody>
    </>
  );
};

const mapStateToProps = ({ authUser }) => {
  const { accessToken } = authUser;
  return { accessToken };
};

export default connect(mapStateToProps)(AddCustomQuestionModal);